.create-button,
.login-button,
.next-button,
.prev-button,
.save-button {
  &:extend(.ui.button);
  &:extend(.ui.primary.button);
  text-transform: uppercase;

  &:focus{
    &:extend(.ui.primary.button:focus);
  }
  &:hover{
    &:extend(.ui.primary.button:hover);
  }
}

.cancel-button {
  &:extend(.ui.button);
  text-transform: uppercase;

  &:focus{
    &:extend(.ui.button:focus);
  }
  &:hover{
    &:extend(.ui.button:hover);
  }
}